import { MainService } from './../../services/main.service';
import { Utils } from './../../services/utils.service';
import * as $ from "jquery";

import { Component, OnInit, Input } from '@angular/core';

@Component({
    selector: 'app-user-info',
    templateUrl: './user-info.component.html',
    styleUrls: ['./user-info.component.scss']
})
export class UserInfoComponent implements OnInit {

    @Input() id;
    @Input() username;
    @Input() user;

    public utils = Utils;
    public isAnonUser: boolean = false;
    public anonUser = {
            name: "Anonymous",
            lastName: "",
            anonNickname: "Anonymous",
            photo: "assets/in2art/imatges/profileDefaultSolidGray.png",
    }

    constructor(public main: MainService) { }

    async ngOnInit() {
        // TACATÁ (skeleton (?)) // 
        if(this.user?.username != "Anonymous" && this.user?.profile) {
            if (this.id) this.user = await this.main.provider.getUserById(this.id); //use userProfileID
            if (this.username) this.user = await this.main.provider.getUserByUsername(this.username);
            if (this.user) this.user = await this.main.provider.getUserById(this.user.userProfileId); //use userProfileID
        }else if (this.user == null || this.user == undefined){
            this.isAnonUser = true;
            this.user = this.anonUser;
        }
        


    }

}
